var myDate = new Date();
var time = myDate.getHours();
var timeline;
/*
1 -> morning
2 -> afternoon 
3 -> 
*/
if (time> 0 &&time < 12) {
  timeline = 1
} else if (time > 12 && time <15) {
  timeline = 2
} else if (time == 12) {
  timeline = 4
} else if ( time >=15 && time <20 ){
  timeline = 3
} else if (time>=20 && time <=24){
  timeline = 5
} else {
  timeline = 6
}
